body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: #333333;
    height: 100vh;
    overflow-x: hidden;
}

iframe {
    width: 21cm;
    left: calc(50% - 15.75cm);
    position: absolute;
    overflow: visible;
    border: 0;
    padding: 0.3125cm 0;
}

::-moz-selection {
    background-color: rgba(68, 136, 255, 0.25);
}

#open {
    visibility: hidden;
}

#options {
    padding: 8px;
    display: inline-block;
    position: absolute;
    z-index: 1;
    display: none;
    left: calc(50vw + 5.25cm);
    width: 10.5cm;
}

#options.focus {
    display: block;
}

#first, #second {
    display: block;
    font-family: -apple-system, sans-serif;
    font-size: 12pt;
    padding: 8px;
    border: 2px solid white;
    outline: none;
    margin: 0;
    color: white;
    position: relative;
    word-break: break-word;
    white-space: pre-wrap;
}

#first:focus, #second:focus {
    border-color: #4488FF;
}

#second {
    margin-top: 12px;
}

#second[data-placeholder="undefined"] {
    display: none;
}

#first::before, #second::before {
    content: attr(data-placeholder);
    position: absolute;
    padding: 2px;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    font-size: 9pt;
    background-color: #333333;
}

#second[data-placeholder="text"] + #display {
    display: none;
}

#display {
    -moz-appearance: none;
}

#display + span {
    position: absolute;
    top: 0;
    z-index: 1;
}

#display:checked + span {
    text-align: center;
}


#display + span:after {
    padding: 2px;
    background-color: #333333;
}

#display:checked + span::after {
    content: 'display';
}

#display:not(:checked) + span::after {
    content: 'inline';
}

#label {
    color: white;
    font-size: 9pt;
    position: relative;
    display: block;
    margin-top: 12px;
}

#display + span + div {
    border: 1px solid white;
    width: 100%;
    display: block;
    content: '';
    position: absolute;
    width: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

#display:focus + span + div {
    border-color: #4488FF;
}

#files div {
    background-color: white;
    margin: 1em;
    font-family: 'georgia', serif;
    color: #333333;
}

#files div div {
    margin-left: 1.25cm;
}

#files a {
    text-decoration: inherit;
    color: inherit;
}
